	module.exports = {"data":{"post":{"html":"<p>Christopher has apparently decided he doesn't like staying up late anymore, and has unilaterally changed his sleep schedule. His routine has now shifted by 90 minutes, so instead of going to sleep at 9 and waking up at 7, he now goes to sleep at 7:30 and wakes up at 5:30. It's like he's now 2 time zones ahead of us, and as you can imagine this has thoroughly confused our morning routine. We've been getting up at 5:30 anyway, so it's not like we're having to get up earlier. It's just that we were used to peace and quiet in the morning. No longer.</p>","fields":{"title":"Time Shift","date":"28 Feb 2003","url":"https://papascott.de/2003-02-28-time-shift/"}}},"pathContext":{"slug":"/2003-02-28-time-shift/","prev":{"fields":{"title":"Ticket To Vienna","date":"28 Feb 2003","slug":"/2003-02-28-ticket-to-vienna/"}},"next":{"fields":{"title":"Today's Headlines","date":"03 Mar 2003","slug":"/2003-03-03-todays-headlines/"}}}}

